import { useState } from "react";

export function TextEditor({ children }) {
  const [show, setShow] = useState(false);
  const displayText = show
    ? children
    : children.split(" ").slice(0, 45).join(" ") + "...";
  return (
    <div>
      {displayText}
      <button onClick={() => setShow((s) => !s)}>
        {show ? "Show less" : "Show more"}
      </button>
    </div>
  );
}
